.hero {
	background-image: url(/dist/img/energy-poverty_opt.jpg);
	background-size: cover;
	background-repeat: no-repeat;
	background-position-x: 75%;
	display: flex;
	color: white;
	font-family: 'Heebo', sans-serif;
	font-size: 1.2rem;
	height: 100vh;
	width: 100%;
	&.mobile {
		@include bp-med {
			display: none;
		}

		justify-content: space-between;
		flex-direction: column;

		.psudo-header {
			display: flex;
			align-items: center;
			justify-content: space-between;
			h1 {
				margin-left: 5%;
			}
			.hamburger {
				width: 50px;
				height: 50px;
				position: relative;
				background-color: rgba($color: #000000, $alpha: 0);
				border: 5px solid rgba($color: #000000, $alpha: 0);
				cursor: pointer;
				transition: all 0.3s ease;
				margin-right: 5%;
				margin-top: 2%;
				@include bp-med {
					display: none;
				}
				div {
					height: 2px;
					width: 30px;
					background-color: white;
					position: absolute;
					transition: all 0.3s ease;
					&.hamburgerLineOne {
						top: 7px;
						left: 5px;
					}
					&.hamburgerLineTwo {
						top: 17px;
						left: 5px;
					}
					&.hamburgerLineThree {
						top: 27px;
						left: 5px;
					}
				}
				&:hover {
					border-radius: 50%;
					transition: all 0.3s ease;
					.hamburgerLineOne {
						transform: translateY(10px) rotate(45deg);
						transition: all 0.5s ease;
					}
					.hamburgerLineTwo {
						opacity: 0;
						transition: all 0.2s ease;
					}
					.hamburgerLineThree {
						transform: translateY(-10px) rotate(-45deg);
						transition: all 0.5s ease;
					}
				}
				&.showing {
					display: block;
					max-height: 1000px;
					transition: 0.5s ease;
				}
			}
		}
		nav {
			display: none;
			&.showing {
				display: block;
				max-height: 1000px;
				transition: 0.5s ease;
			}
			@include bp-med {
				display: flex;
			}
		}
		.logo {
			width: 153px;
			enable-background: new 0 0 159.9 105;
			margin: 5% auto;
		}
	}

	&.desktop {
		display: none;
		@include bp-med {
			display: flex;
			flex-direction: column;
			justify-content: space-between;

			.psudo-header {
				display: flex;
				flex-direction: row-reverse;
				justify-content: flex-end;
				align-items: center;
				margin: 3% 10%;
				.logo {
					width: 150px;
				}
				h1 {
					font-size: 2rem;
					margin-left: 3%;
				}
			}
			nav {
				display: flex;
				justify-content: flex-end;
				font-size: 1.7rem;
				ul {
					display: flex;
					justify-content: space-around;
					width: 45%;

					margin: 1% 5%;
					li {
						margin: 1%;
						a {
							color: white;
							text-decoration: none;
							&.active {
								border-bottom: 2px solid white;
							}
						}
					}
				}
			}
		}
	}
}

